@import '../../constants/index.scss';

.gradian-btn {
  gap: 6px;
  border: none;
  height: 40px;
  display: flex;
  padding: 0 14px;
  font-size: 14px;
  font-weight: 600;
  line-height: 18px;
  text-align: center;
  border-radius: 8px;
  align-items: center;
  color: $color-primary-content;
  font-family: "Manrope", sans-serif;
  background: linear-gradient(90deg, $color-secondary 0%, $color-primary 100%);
}

.fundiin-btn {
  gap: 4px;
  height: 32px;
  display: flex;
  padding: 0 8px;
  font-size: 14px;
  font-weight: 600;
  line-height: 18px;
  text-align: center;
  border-radius: 6px;
  align-items: center;
  background-color: transparent;
  border: 1px solid $color-grey-200;
  font-family: "Manrope", sans-serif;

  &.active {
    border-color: $color-primary;
  }
}